import React, { useEffect, useState } from "react";
import { useAuth0 } from "@auth0/auth0-react";
import { Button } from 'antd';

export const Component = () => {
    const {
      getAccessTokenSilently,
      loginWithRedirect,
      isLoading,
      isAuthenticated
    } = useAuth0();
    const [lineItems, setLineItems] = useState([]);

    useEffect(() => {

      if (!isAuthenticated) return;

      const asyncGetLineItems = async () => {
        const accessToken = await getAccessTokenSilently();

        const nextLineItems = await fetch(
          'https://5qfry2gbjh.execute-api.eu-west-1.amazonaws.com/dev/getSubmissions',
          {
            headers: {
              'Authorization': `Bearer ${accessToken}`,
            },
          }
        ).then(response => response.json());

        console.log('nextLineItems')
        console.log(nextLineItems)

        setLineItems(nextLineItems);
      };

      asyncGetLineItems();

    }, [isAuthenticated]);

    if (isLoading) {
      return null
    }

    if (!isAuthenticated) {
      return (
        <Button size="large" onClick={() => loginWithRedirect()}>Log In</Button>
      );
    }

    return (
      <div>
        {lineItems.map(() => <p>Item</p>)}
      </div>
    );

}

Component.displayName = 'App';
